import { css, polished } from 'jimu-core';
export const sortStyle = (props) => {
    const theme = props.theme;
    const { emptyHintTopSpaceSize } = props;
    const emptyHintTopSpace = emptyHintTopSpaceSize ? polished.rem(emptyHintTopSpaceSize - 16) : polished.rem(70);
    return css `
    width:100%;
    .sort-title {
        margin-bottom: 20px;
        color: ${theme.ref.palette.black};
        font-size: 16px;
    }
    .padding-0 {
        padding: 0;
    }
    .ml-0 {
        margin-left: 0;
    }
    .delete-all-button {
        cursor: pointer;
    }
    .jimu-tree-item__body {
        margin-bottom: 10px;
        background: ${theme.ref.palette.neutral[500]};
        padding: 10px 10px 10px 0;
        box-shadow: 0 0 0 2px ${polished.rgba(theme.ref.palette.white, 0.3)};
        flex: 1;
    }
    .multi-sort-select-con {
        & {
            display: flex;
            align-items: center;
            flex: 1;
        }
        .sort-field-selector {
            background:${theme.sys.color.secondary.light};
            border-radius: 2px;
            flex: 1;
            width: 0
        }

        .delete-button {
            cursor: pointer;
            text-align:right;
            margin-left: ${polished.rem(2)};
        }
        .delete-button svg {
            margin-right:0;
        }
    }
    .delete-sort-con {
        & {
            margin-left:5px;
        }
        .sort-button-l {
            border-radius: 2px 0 0 2px;
            border-right:none;
        }
        .sort-button-r {
            border-radius: 0px 2px 2px 0px;
            border-left:none;
        }
        .sort-button {
            border-color: ${theme.sys.color.divider.primary};
        }
        svg {
            margin-right:0;
        }
    }
    .add-option .add-option-button{
        & {
            width:100%;
            font-size:14px;
            display: block;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            margin-bottom: ${polished.rem(16)};
        }
        svg {
            margin-right:0;
            margin-right:5px;
        }
    }
    .no-sort-remind {
        margin: ${emptyHintTopSpace} auto 0;
        color: ${theme.ref.palette.neutral[700]};
        font-size: 13px;
        text-align: center;
    }
  `;
};
