import { css } from 'jimu-core';
export const inputTypeSelectorStyle = (props) => {
    const theme = props.theme;
    return css `
    .style-button{
    }
    .style-warning{
      color: ${theme.sys.color.warning.main};
    }
    .style-popper{
      .style-popper-content{
        width: 180px;
        display: flex;
        flex-wrap: wrap;
        padding: 10px;
        max-height: 300px;
        height: auto;
        overflow: auto;

        .style-item{
          margin: 5px;
          padding: 0;
          background-color: ${theme.ref.palette.neutral[300]};

          &.active {
            outline: 2px solid ${theme.sys.color.primary.main};
          }
        }
      }

    }
  `;
};
