/** @jsx jsx */
import { css } from 'jimu-core';
export function sqlExpressionClauseStyles(props) {
    const theme = props.theme;
    return css `
    .sql-expression-single {
      background-color: ${theme.ref.palette.neutral[500]};
      padding: 13px 5px;
      margin-bottom: 0.5rem;

      .sql-expression-clause{
        display: flex;
        .clause-main-container{
          display: flex;
          .clause-field-and-operator{
            display: flex;
            .clause-field-container,
            .clause-operator-container{
              margin: 0 4px;
            }
          }
          .clause-style-container{
            display: flex;
            flex-grow: 1;
            margin: 0 4px;
            .source-type-selector{
              margin-right: 8px;
              .jimu-btn{
                &.jimu-dropdown-button-icon-dot .icon-btn-sizer:after {
                  border-top: 2px solid #fff;
                }
                .jimu-icon {
                  color: #fff !important;
                }
              }
            }
          }
        }

        .clause-controllers-container{
          width: 150px;
          margin-left: 12px;
          margin-right: 1px;
          .case-sensitive-btn, .more-input-setting-btn, .duplicate-btn, .delete-btn{
            margin: 0 2px;
            .jimu-badge-badge {
              border-color: ${theme.ref.palette.black};
            }
          }
          .case-sensitive-btn.disabled, .case-sensitive-btn.disabled:hover, .case-sensitive-btn:disabled:hover{
            border: none;
          }
          .case-sensitive-btn:disabled .jimu-icon, .more-input-setting-btn:disabled .jimu-icon{
            opacity: 0.5;
          }
          .more-input-setting-btn-active,
          .dropdown-active {
            background-color: ${theme.sys.color.primary.main} !important;
            color: ${theme.ref.palette.black} !important;
          }
        }
      }

      .ask-for-value-container{
        margin-top: 1.5rem;
        margin-left: 8px;
        .ask-for-value-radio{
          display: flex;
          margin: 10px 0;
        }
        .ask-for-value-options{
          display: flex;
          margin-left: 20px;
          .ask-for-value-item{
            .ask-for-value-item-label{
              color: ${theme.ref.palette.neutral[900]};
              margin-bottom: 3px;
            }
          }
          .ask-for-value-middle-item{
            margin: 0 0.1rem;
          }
          .use-label-input {
            margin-left: 0.5rem;
            width: 25%;
          }
        }
      }

      &.for-group-filter {
        .sql-expression-clause {
          .clause-main-container .clause-style-container .input-editor {
            margin: 0 2px;
          }
          .clause-controllers-container {
            width: unset;
            margin: 0;
          }
        }
        .ask-for-value-container {
          .ask-for-value-radio-selector {
            display: block !important;
            .ask-for-value-middle-item {
              width: 100%;
              .style-type-selector {
                width: 100%;
                .style-button {
                  width: 100%;
                  button {
                    width: 100%;
                  }
                }
              }
            }
          }
          .ask-for-value-options {
            display: block;
            flex-direction: column;
            .ask-for-value-item {
              display: block;
            }
            .use-label-input {
              margin-left: 1.5rem;
              margin-top: 0.5rem;
              width: auto;
            }
          }
        }
      }
    }
  `;
}
