import { css, polished } from 'jimu-core';
export function groupSqlExpressionBuilderStyles(props) {
    const theme = props.theme;
    return css `
    .group-sql-expression-builder {

      margin: 0.875rem;
      margin-bottom: 0;
      .no-data-tip{
        text-align: center;
      }
      .content-container {
        min-height: 425px;
        .fields-container {
          width: 623px;
          .fields-container-label {
            font-size: 16px;
            font-weight: 600;
            color: ${theme.ref.palette.neutral[1000]};
          }
          .type-label {
            font-size: 14px;
            font-weight: 600;
            color: ${theme.ref.palette.neutral[900]};
          }
          .main-field {
            padding-right: 0.75rem;
            margin-bottom: 1.5rem;
          }
          .field-list {
            padding-right: 0.75rem;
            max-height: calc(100vh - 380px);
            overflow-y: auto;
          }
          .field-item {
            background-color: ${theme.ref.palette.neutral[500]};
            padding: 13px 12px;
            margin-bottom: 0.5rem;
            .data-field-container {
              .data-source-selector {
                width: 240px;
                position: relative;
                .data-source-selector-inner {
                  width: 100%;
                  position: absolute;
                  top: -6px;
                  margin-left: 0.25rem;
                  .main-ds-label {
                    color: ${theme.ref.palette.neutral[1100]};
                  }
                  .view-ds-label {
                    margin-top: 0.125rem;
                    font-size: 12px;
                    color: ${theme.ref.palette.neutral[900]};
                  }
                }
              }
              .field-selector {
                width: 206px;
              }
              .and-or-container {
                width: 75px;
              }
            }
            .case-sensitive-container {
              width: 120px;
              display: flex;
              justify-content: end;
              .case-sensitive-btn {
                overflow: visible;
              }
              .case-sensitive-btn, .more-input-setting-btn, .duplicate-btn, .delete-btn{
                .jimu-badge-badge {
                  border-color: ${theme.ref.palette.black};
                }
              }
              .case-sensitive-btn.disabled, .case-sensitive-btn.disabled:hover, .case-sensitive-btn:disabled:hover{
                background: ${theme.sys.color.secondary.main};
                background-color: ${theme.ref.palette.neutral[500]};
                border: none;
              }
              .case-sensitive-btn:disabled .jimu-icon {
                opacity: 0.5;
              }
              .jimu-badge-badge:not(.jimu-badge-badge-color-primary) {
                top: -2px;
              }
            }
          }
        }
        .clause-container {
          width: 239px;
          margin-top: 42px;
          margin-left: 0.75rem;
          padding-left: ${polished.rem(19)};
          border-left: 2px solid ${theme.ref.palette.neutral[500]};
          .clause-container-label {
            font-size: 14px;
            font-weight: 600;
            margin-bottom: -0.5rem;
            color: ${theme.ref.palette.neutral[900]};
          }
          .sql-expression-single.for-group-filter {
            padding-top: 10px;
            padding-left: 0;
            padding-right: 0;
            background-color: transparent;
            .clause-main-container {
              display: block;
              width: 100%;
              .clause-operator-container {
                width: 100%;
                margin: 0;
              }
              .clause-style-container {
                margin: 0;
                margin-top: 0.75rem;
                .input-editor {
                  width: calc(100% - 35px) !important;
                }
              }
            }
            .ask-for-value-container {
              margin-left: 0;
              margin-right: 0;
            }
          }
        }
      }
    }
  `;
}
